/*
0-600X: Phone
600-900px: Tablet portrait
900-1200px: tablet landscape
1200-1800 : is where our normal styles apply
1800px+ Big desktop
*/

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
}

body {
  box-sizing: border-box;
  padding: 3rem;
  font-family: calibri;
  background-color: rgb(221, 213, 213);
}
@media (max-width: 1200px) {
  body {
    padding: 1.5rem;
  }
}
@media (max-width: 900px) {
  body {
    padding: 0rem;
  }
}
@media (max-width: 600px) {
  body {
    padding: 0;
    background-color: rgba(36, 24, 199, 0.932);
  }
}
.profile-card {
  width: 75vw;
  height: 80vh;
  padding: 2rem;
  margin: auto;
  border-radius: 5rem;
  background-color: rgba(36, 24, 199, 0.932);
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  /**Find a way to center profile card**/
}
@media (max-width: 1200px) {
  .profile-card {
    width: 90vw;
    height: 90vh;
  }
}
@media (max-width: 900px) {
  .profile-card {
    padding: 1.5rem;
    width: 100vw;
    height: 100vh;
    border-radius: 0;
  }
}
@media (max-width: 600px) {
  .profile-card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem;
    width: 100vw;
    height: 100vh;
    border-radius: 0;
    background-color: inherit;
  }
}
.profile-card-left {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.avatar-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.profile-avatar {
  width: 80%;
  margin: auto;
  border-radius: 50%;
}
@media (max-width: 600px) {
  .profile-avatar {
    width: 50%;
    margin: auto;
  }
}
.time {
  font-size: 1.6rem;
  margin: 1rem;
}
.profile-card-links {
  display: flex;
  justify-content: space-around;
  padding: 1rem;
  margin: 1rem;
}
.page-link {
  text-decoration: none;
  padding: 1rem 1.5rem;
  font-size: 1.5rem;
  background-color: black;
  color: white;
  border-radius: 10rem;
  transition: all 0.3s;
}
.page-link:hover {
  transform: translateY(-0.5rem) scale(1.1);
  background-color: white;
  color: black;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
.page-link:active,
.page-link:focus {
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.user-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  width: 100%;
}
@media (max-width: 1200px) {
  .user-details {
    width: auto;
  }
}
@media (max-width: 900px) {
  .user-details {
    width: auto;
  }
}
.user-name {
  font-size: 2.5rem;
  align-self: center;
  margin: 1rem;
}
@media (max-width: 900px) {
  .user-name {
    font-size: 2.5rem;
    margin: 0.5rem;
  }
}
@media (max-width: 600px) {
  .user-name {
    font-size: 2rem;
    margin: 0.3rem;
  }
}
.user-bio {
  font-size: 2rem;
  width: 90%;
  margin: 1rem;
  align-self: center;
}
@media (max-width: 1200px) {
  .user-bio {
    font-size: 1.8rem;
  }
}
@media (max-width: 900px) {
  .user-bio {
    font-size: 1.6rem;
  }
}
@media (max-width: 600px) {
  .user-bio {
    font-size: 1.5rem;
  }
}
.user-social-links {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 1rem;
}
@media (max-width: 900px) {
  .user-social-links {
    margin: 0.5rem;
  }
}
.social-links-heading {
  font-size: 2rem;
}
@media (max-width: 1200) {
  .social-links-heading {
    font-size: 2rem;
  }
}
@media (max-width: 900px) {
  .social-links-heading {
    font-size: 1.8rem;
  }
}
@media (max-width: 600px) {
  .social-links-heading {
    font-size: 1.6rem;
  }
}
.social-links-container {
  list-style: none;
  padding: 1rem;
  margin: 1rem;
  width: 70%;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1200px) {
  .social-links-container {
    padding: 0.75rem;
    margin: 0.75rem;
  }
}
@media (max-width: 900px) {
  .social-links-container {
    padding: 0.5rem;
    margin: 0.5rem;
    width: 80%;
  }
}
@media (max-width: 600px) {
  .social-links-container {
    width: 90%;
    align-self: center;
  }
}
.social-link {
  text-decoration: none;
  background-color: black;
  color: white;
  display: inline-block;
  position: relative;
  font-size: 1.6rem;
  border: none;
  cursor: pointer;
  padding: 1rem 1.5rem;
  border-radius: 10rem;
  transition: all 0.3s;
}
@media (max-width: 1200px) {
  .social-link {
    font-size: 1.5rem;
  }
}
@media (max-width: 900px) {
  .social-link {
    font-size: 1.4rem;
    padding: 0.75rem 1.25rem;
  }
}
@media (max-width: 600px) {
  .social-link {
    font-size: 1.2rem;
    padding: 0.5rem 1rem;
  }
}
.social-link:hover {
  transform: translateY(-0.5rem) scale(1.1);
  background-color: white;
  color: black;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
.social-link:active,
.social-link:focus {
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}

.user-attributes {
  display: flex;
  flex-direction: column;
}
.user-attributes-heading {
  font-size: 2rem;
}
@media (max-width: 900px) {
  .user-attributes-heading {
    font-size: 1.8rem;
  }
}
@media (max-width: 600px) {
  .user-attributes-heading {
    font-size: 1.6rem;
  }
}
.user-attributes-container {
  display: flex;
  justify-content: space-around;
  align-self: center;
  width: 80%;
  margin: 1rem;
}
/* Find a way to eliminate repeated code in hobbies and dislikes*/
.user-hobbies {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.user-hobbies-heading {
  font-size: 1.8rem;
}
.user-hobbies-list {
  list-style: none;
  margin: 0.5rem;
  font-size: 1.5rem;
}
.user-dislikes {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.user-dislikes-heading {
  font-size: 1.8rem;
  font-weight: bold;
}
.user-dislikes-list {
  list-style: none;
  margin: 0.5rem;
  font-size: 1.5rem;
}

/* Stylng for 'ABOUT ME' page */
.about-page {
  width: 60vw;
  height: auto;
  background-color: rgba(36, 24, 199, 0.932);
  /* background-color: white; */
  margin: auto;
  border-radius: 5rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  font-size: 1.6rem;
}
.about-bio {
  display: flex;
  flex-direction: column;
  align-self: center;
  width: 80%;
  margin: 1rem;
}
.about-bio-heading {
  font-size: 2rem;
}
.about-bio-text {
  display: inline-block;
  width: 70%;
  margin: auto;
}
.about-goals {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
.about-goals-heading {
  font-size: 2rem;
}
.about-goals-list {
  display: flex;
  padding: 1rem;
  margin: 1 rem;
}
.about-goals-list-item {
  list-style: none;
  padding: 0.5rem;
  margin: 0.5rem;
}
.about-confidence {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin: 1rem;
}
.about-confidence-heading {
  font-size: 2rem;
}
.about-confidence-list {
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  margin: 0.5rem;
  list-style: none;
}
.about-future-note {
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
.about-future-note-heading {
  font-size: 2rem;
}
.about-future-note-text {
  display: inline-block;
  width: 70%;
  margin: auto;
}
.about-extra {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  margin: 1rem;
}

/*Styling for 'CONTACT US' page*/
.form-container {
  width: 70vw;
  display: flex;
  flex-direction: column;
  background-color: white;
  margin: auto;
  border-radius: 5rem;
}
.form-heading {
  margin: 1rem;
  align-self: center;
}
.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 70%;
  height: auto;
  margin: auto;
  padding: 2rem;
}
.contact-input-field {
  padding: 1rem;
  border: 1px solid black;
  border-radius: 1rem;
}
.contact-input-field:focus {
  border: 1px blue;
}
.contact-input-label {
  font-style: italic;
  font-size: 1.5rem;
  margin: 0.5rem;
}
.contact-submit {
  width: 20%;
  padding: 1rem 1.5rem;
  margin: 2rem;
  align-self: center;
  font-size: 1.5rem;
  background-color: rgba(36, 24, 199, 0.932);
  color: white;
  border-radius: 5rem;
  border: none;
  transition: all 0.3s;
  cursor: pointer;
}
.contact-submit:hover {
  transform: translateY(-0.5rem) scale(1.1);
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.2);
}
.contact-submit:active,
.contact-submit:focus {
  transform: translateY(-1px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.2);
}
.contact-success {
  display: none;
}
